import React, { Component} from 'react';
import nextId from "react-id-generator";
import strings from "../../Localisation";
import {sendAmplitudeData} from "../../../utils/Amplitude";

export default class MoreInfo extends Component {
    constructor(props) {
        super(props);
        this.buttonId = nextId();
    }

    componentDidMount() {
        let link = this;
        let button = document.getElementById(this.buttonId);

        button.addEventListener('click', function (event) {
            event.preventDefault();

            if (link.props.type === "info") {
                sendAmplitudeData("HowItWorks: opened youtube video");
            }

            else {
                sendAmplitudeData("HowItWorks: opened info page");
            }

            const openWhere = (link.props.path[0] === "/")? "_self" : "_blank";
            window.open(link.props.path, openWhere);
        })
    }

    render() {
        let imagePath = "/images/main/functional/play.svg";

        if (this.props.type === "info") {
            imagePath = "/images/main/functional/info.svg";
        }

        return (
            <div className={"functional__more_info"} id={this.buttonId}>
                <div className={"functional__more_info__title"}>
                    {
                        (this.props.type === "info")?
                            strings.functional__more_info
                            :
                            strings.functional__how_it_works
                    }
                </div>

                <img className={"functional__more_info__image"} src={imagePath}/>
            </div>
        )
    }
}